<template>
  <div class="searchBar">
    <b-input-group class="mt-5">
        <b-dropdown slot="prepend" :text="chosen_text" variant="dark" >
          <b-dropdown-item @click="choose_item(item.name)" v-for="item in cate_list" :key="item.id">{{item.name}}</b-dropdown-item>
        </b-dropdown>
        <b-form-input placeholder="请输入要搜索的内容" v-model="searchContent" @keydown.enter="searchResult"></b-form-input>
        <b-input-group-append>
          <b-button variant="outline-dark" @click="searchResult">搜索</b-button>
        </b-input-group-append>
    </b-input-group>
  </div>
</template>

<script>
export default {
  name: 'searchBar',
  data () {
    return {
      chosen_text: '全部',
      searchContent: this.msg == '' ? '' : this.msg,
      cate_list: [
        {id: 1,name: '抗菌剂'},
        {id: 2,name: '柔软剂'},
        {id: 3,name: '阻燃剂'},
        {id: 4,name: '抗皱整理剂'},
        {id: 5,name: '抗紫外线剂'},
        {id: 6,name: '抗静电剂'},
        {id: 7,name: '防水防油剂'},
        {id: 8,name: '全部'},
      ]
    }
  },
  props:{
    msg: String
  },
  methods: {
    choose_item (name) {
      if(name != '全部'){
        this.chosen_text = name
        this.searchContent = name
      }else{
        this.chosen_text = name
        this.searchContent = ''
      }
      
    },
    searchResult () {
      if(this.searchContent != '' && this.searchContent != null){
        this.$router.push({ path: `/search/${this.searchContent}` })
      }else{
        alert('输入不能为空!');
      }
      
    }
  }
}
</script>
