<template>
  <div class="apitest">
    <b-container class="bv-example-row">
      <b-row class="mt-5">
        <b-col cols="18" md="12">
          <SearchBar ref="SearchBar" :msg="serarch_Content" v-model="serarch_Content"></SearchBar>
        </b-col>
      </b-row>
      
      <b-row class="mt-5" v-show="isVisable">
        <b-col cols="18" md="12">
          <div class="text-center">
            <b-spinner variant="dark" style="width: 3rem; height: 3rem;" type="grow" label="Text Centered"></b-spinner>
          </div>
        </b-col>
      </b-row>

      <div v-for="item in detailsList" v-bind:key="item.detailId" v-show="!isVisable">
        <SearchResultItem :item="item" class="resItemStyle"/>
      </div>
      
      <b-row class="mt-2">
        <b-col cols="18" md="12">
          <div class="mr-0">
              <b-pagination-nav
              :number-of-pages="pageInfo.pages"
              first-text="First"
              prev-text="Prev"
              next-text="Next"
              last-text="Last"
              align="center"
              use-router
              :link-gen="linkGen"
              @input="onChange($event)"
            ></b-pagination-nav>
          </div>
        </b-col>
      </b-row>
    </b-container>   
  </div>
</template>

<script>
import SearchBar from '@/components/SearchBar.vue'
import SearchResultItem from '@/components/SearchResultItem'
export default {
  name: 'apitest', //name为文件名的全小写
  data () {
    return{
      detailsList: '',
      search_id: '',
      serarch_Content: this.$route.params.searchContent,
      isVisable:true,
      pageInfo: {},
    }
  },
  mounted () {
    //this.$http this.axios
    //抽离api
    switch(this.$route.params.searchContent) {
     case "抗静电剂":
        this.search_id = 1;
        break;
     case "阻燃剂":
        this.search_id = 2;
        break;
     default:
        this.search_id = null
    } 
    var api = `https://www.domyown.top/ssm/details/${this.search_id}`
    this.axios
      .get(api,{
        params: {
          pn: 1
        }
      })
      .then(response => {
        this.isVisable = false
        this.detailsList = response.data.extend.pageInfo.list
        this.pageInfo = response.data.extend.pageInfo
      })
      .catch(error => (console.log(error)))
  },
  beforeRouteUpdate(to, from, next){
    this.isVisable = true
    next()
    this.doSearch()
  },
  methods:{
    doSearch () {
      switch(this.$route.params.searchContent) {
      case "抗静电剂":
          this.search_id = 1;
          break;
      case "阻燃剂":
          this.search_id = 2;
          break;
      default:
          this.search_id = null
      } 
      var api = `https://www.domyown.top/ssm/details/${this.search_id}`
      this.axios
        .get(api,{
          params: {
            pn: 1
          }
        })
        .then(response => {
          this.isVisable = false
          this.detailsList = response.data.extend.pageInfo.list
          this.pageInfo = response.data.extend.pageInfo
        })
        .catch(error => (console.log(error)))
      },
      onChange (page) {
        if(page === null){
          page = 1
        }
        var api = `https://www.domyown.top/ssm/details/${this.search_id}`
        this.axios
          .get(api,{
            params: {
              pn: page
            }
          })
          .then(response => {
            this.isVisable = false
            this.detailsList = response.data.extend.pageInfo.list
            this.pageInfo = response.data.extend.pageInfo
          })
          .catch(error => (console.log(error)))
      },
      linkGen (pageNum) {
        return pageNum === 1 ? '' : `?pn=${pageNum}`
      }

  },
  components: {
    SearchBar,
    SearchResultItem
  }
}
</script>

<style lang="less" scoped>
.resItemStyle{
  border-bottom: 1px #eee solid;
}
</style>