<template name="searchResultItem">
    <div class="searchResultItem">
       <b-row class="mt-5">
        <b-col cols="18" md="12">
           <ul class="list-unstyled">
              <b-media tag="li" right-align vertical-align="center" class="li_style">                
                <div @click="toDeatilsPage" class="mt-0 mb-1">
                  <label class="title">{{ item.cnName }}</label>
                </div>
                <div>
                  <b-badge pill variant="success" class="mr-2">{{ item.reagent.reagentType }}</b-badge>
                  <b-badge pill variant="secondary" class="mr-2">{{item.aliasesName}}</b-badge>
                  <b-badge pill variant="warning" class="mr-2">{{item.company}}</b-badge>
                </div>
                <div class="mb-1 performance font_style">
                  <span class="tag_style">【性能】</span>{{item.performance}}
                </div>
                <div class="mb-1 purpose font_style">
                  <span class="tag_style">【用途】</span>{{item.purpose}}
                </div>
                <div class="mb-1 tech_style font_style">
                  <span class="tag_style">【染整工艺】</span>
                  {{item.technology}}
                </div> 
              </b-media>          
          </ul>
        </b-col>
      </b-row>
    </div>
</template>

<script>
export default {
  name: 'searchResultItem',
  props: {
    item: Object
  },
  methods:{
    toDeatilsPage () {
      this.$router.push({ name: 'details', params: {type: this.item.reagent.reagentType, id: this.item.detailId }});
    }
  }
}
</script>

<style lang="less" scoped>
.font_style{
  font-family: 'Microsoft YaHei',arial,tahoma,sans-serif;
  font-size: 0.9rem;
  color: #585858;
}
.tag_style{
  font-family: 'Courier New', Courier, monospace;
  color: #7A7A7A;
  font-size: 1rem;
}
.title{
  font-family: 'Microsoft YaHei',arial,tahoma,sans-serif;
  font-size: 1.2rem;
  color: #333333;
}
.title:hover{
  border-bottom: 2px #333 solid;
  cursor: pointer;
}
.tech_style{
    display: block;
    display: -webkit-box;
    max-width: 1014px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>


